import React, { useState } from "react";
import { View, Text, TouchableOpacity, Modal, StyleSheet } from "react-native";
import { useSelector } from "react-redux";
import { collection, addDoc, serverTimestamp } from "firebase/firestore"
import OrderItem from "./OrderItem";
import db from "../../firebase";

export default function ViewCart({ navigation }) {
  const [modalVisible, setModalVisible] = useState(false);
  const [loading, setLoading] = useState(false);

  const { items, restaurantName } = useSelector(
    (state) => state.cartReducer.selectedItems
  );

  console.log(">>>>>>>> items in view cart", items)

  const total = items
    .map((item) => Number(item.foodPrice.replace("£", "")))
    .reduce((prev, curr) => prev + curr, 0);

  const totalGBP = total.toLocaleString("en", {
    style: "currency",
    currency: "GBP",
  });

  const addOrderToFireBase = async() => {
    setLoading(true);
    // const db = getFirestore(firebase);
    const currenttime = new Date().setHours(new Date().getHours() + 1);
    const collectionRef = collection(db, "orders");
    const docRef =  await addDoc(collectionRef, {
        items: items,
        restaurantName: restaurantName,
        userid : localStorage.getItem('userId'),
        createdAt: serverTimestamp(),
        total: totalGBP,
        status: 'created',
        customerName: localStorage.getItem('name'),
        customeraddress: localStorage.getItem('address'),
        customerpostcode: localStorage.getItem('postcode'),
        customeremail:localStorage.getItem('userData'),
      });
      localStorage.setItem('currentOrder', docRef.id)
      if(localStorage.getItem("userData")){
        navigation.navigate("OrderCompleted");
      }else{
        navigation.navigate("SignInScreen");
      }
      

    //   .then(() => {
    //     setTimeout(() => {
    //       setLoading(false);
    //       navigation.navigate("OrderCompleted");
    //     }, 2500);
    //   });
  };

  const styles = StyleSheet.create({
    modalContainer: {
      flex: 1,
      justifyContent: "flex-end",
      backgroundColor: "rgba(0,0,0,0.7)",
    },

    modalCheckoutContainer: {
      backgroundColor: "white",
      padding: 16,
      height: 500,
      borderWidth: 1,
    },

    restaurantName: {
      textAlign: "center",
      fontWeight: "600",
      fontSize: 18,
      marginBottom: 10,
    },

    subtotalContainer: {
      flexDirection: "row",
      justifyContent: "space-between",
      marginTop: 15,
    },

    subtotalText: {
      textAlign: "left",
      fontWeight: "600",
      fontSize: 15,
      marginBottom: 10,
    },
  });

  const checkoutModalContent = () => {
    return (
      <>
        <View style={styles.modalContainer}>
          <View style={styles.modalCheckoutContainer}>
            <Text style={styles.restaurantName}>{restaurantName}</Text>
            {items.map((item, index) => (
              <OrderItem key={index} item={item} />
            ))}
            <View style={styles.subtotalContainer}>
              <Text style={styles.subtotalText}>Subtotal</Text>
              <Text>{totalGBP}</Text>
            </View>
            <View style={{ flexDirection: "row", justifyContent: "center" }}>
              <TouchableOpacity
                style={{
                  marginTop: 20,
                  backgroundColor: "black",
                  alignItems: "center",
                  padding: 13,
                  borderRadius: 30,
                  width: 300,
                  position: "relative",
                }}
                onPress={() => {
                  addOrderToFireBase();
                  setModalVisible(false);
                }}
              >
                <Text style={{ color: "white", fontSize: 20 }}>Checkout</Text>
                <Text
                  style={{
                    position: "absolute",
                    right: 20,
                    color: "white",
                    fontSize: 15,
                    top: 17,
                  }}
                >
                  {total ? totalGBP : ""}
                </Text>
              </TouchableOpacity>
            </View>
          </View>
        </View>
      </>
    );
  };

  return (
    <>
      <Modal
        animationType="slide"
        visible={modalVisible}
        transparent={true}
        onRequestClose={() => setModalVisible(false)}
      >
        {checkoutModalContent()}
      </Modal>
      {total ? (
        <View
          style={{
            flex: 1,
            alignItems: "center",
            justifyContent: "center",
            flexDirection: "row",
            position: "fixed",
            bottom: 50,
            left: 500,
            zIndex: 999,
          }}
        >
          <View
            style={{
              flexDirection: "row",
              justifyContent: "center",
              width: "100%",
            }}
          >
            <TouchableOpacity
              style={{
                marginTop: 20,
                backgroundColor: "black",
                flexDirection: "row",
                justifyContent: "center",
                padding: 15,
                borderRadius: 30,
                width: 300,
                position: "relative",
              }}
              onPress={() => setModalVisible(true)}
            >
              <Text style={{ color: "white", fontSize: 20, marginRight: 30 }}>
                View Cart
              </Text>
              <Text style={{ color: "white", fontSize: 20 }}>{totalGBP}</Text>
            </TouchableOpacity>
          </View>
        </View>
      ) : (
        <></>
      )}
    </>
  );
}